import React from 'react';
import { FaTwitter, FaGithub, FaLinkedin } from 'react-icons/fa';
import img from '../../me.jpg';

const Icons = (props) => {
    return (
        <div style={{margin: "0 auto", textAlign: "center"}}>
            <ul id="info-icon-list">
                <div className="info-icon">
                    <a href="http://github.com/infocus7/" target="_blank" rel="noopener noreferrer" className="sm_icon">
                        <FaGithub size="6.7%"/>
                    </a>
                </div>

                <div className="info-icon">
                    <a href="https://www.linkedin.com/in/fabiangonz98/" target="_blank" rel="noopener noreferrer" className="sm_icon">
                        <FaLinkedin size="6.7%"/>
                    </a>
                </div>

                <div className="info-icon">
                    <a href="https://twitter.com/fabiangonz98" target="_blank" rel="noopener noreferrer" className="sm_icon">
                        <FaTwitter size="6.7%"/>
                    </a>
                </div>
            </ul>
        </div>
    );
}

