import React, { useState, useEffect } from "react";
import "./App.css";
import SideNav from "./components/side_nav";
import Content from "./components/content";
import UserInfo from "./components/side_nav/user_info";
import NavOpts from "./components/side_nav/nav_opts";

//const minVal = 900;
function getWindowDimensions() {
  const { innerWidth: width, innerHeight: height } = window;
  return { width, height };
}

function useWindowDimensions() {
  const [windowDimensions, setWindowDimensions] = useState(
    getWindowDimensions()
  );

  useEffect(() => {
    const handleResize = () => {
      setWindowDimensions(getWindowDimensions());
    };

    window.addEventListener("resize", handleResize);

    return () => window.removeEventListener("resize", handleResize);
  }, []);

  return windowDimensions;
}

function App() {
  // TODO Possibly remove if not needed for icons.
  // TODO Add some media queries involving height display
  const { height, width } = useWindowDimensions();

  // TODO optimize so it doesn't re-render children when getting window dims...
  const isMiniWidth = width < 900;

  // TODO Keep Side content on side when scrolling.
  // TODO Keep UserInfo + NavOptions in their place when scrolling.
  // if(isMiniWidth)
  //   console.log('IS MINI!')
  // else
  //   console.log('IS BIG!')
  return (
    <div id="app">
      <div id="view">
        {isMiniWidth ? (
          <>
            <UserInfo isMini={isMiniWidth} id="user-info"/>
            <Content isMini={isMiniWidth} />
            <NavOpts isMini={isMiniWidth} id="nav-opts"/>
          </>
        ) : (
          <>
            <SideNav isMini={isMiniWidth} />
            <Content isMini={isMiniWidth} />
          </>
        )}
      </div>
    </div>
  );
}

export default App;
