import React from 'react';
import UserInfo from './side_nav/user_info'
import NavOpts from './side_nav/nav_opts'

export default function SideNav(props) {
    return(
        <div id="side-nav">
            <UserInfo
                isMini={props.isMini}
                id="user-info"
            />

            <div>
                <NavOpts
                    isMini={props.isMini}
                    id="nav-opts"
                />
            </div>

            <>
            </>
        </div>
    );
};